<template>
  <van-list
    class="cart"
    v-model="lazyLoad.isloading"
    :finished="lazyLoad.isFinished"
    :finished-text="lazyLoad.finishedText"
    :offset="50"
    @load="getLoad"

  >
  <van-nav-bar title="Ë¥≠Áâ©ËΩ¶" :right-text="isManage ? 'ÁÆ°ÁêÜ' : 'ÂÆåÊàê'"  @click-right="manage"/>
    <!-- Ë¥≠Áâ©ËΩ¶Êó†Êï∞ÊçÆÂ∞±ÊòæÁ§∫Á©∫È°µÈù¢ -->
    <div v-if="this.cartLimitData.length == 0">
      <van-empty
        class="custom-image"
        image="https://img.yzcdn.cn/vant/custom-empty-image.png"
        description="Âø´ÂéªÈÄõÈÄõÂä†Êª°‰Ω†ÁöÑË¥≠Áâ©ËΩ¶Âêßüíó"
      />
    </div>
    
    <!-- ËÆæÁΩÆÂïÜÂìÅÂàóË°®Êù°swipercellÁªÑ‰ª∂ van-listÂàóË°®ÊáíÂä†ËΩΩ-->
    <div  class="list" >
      <van-swipe-cell class="list-items" v-for="(item,index) in cartLimitData" :key="index">
        <div class="list-content clearfix">
          <!-- ÂçïÈÄâÊ°Ü -->
          <div class="fl checkbox-item">
            <van-checkbox shape="round" v-model="item.isCheck" @change="simpleSelect"/>
          </div>
          <!-- ÂïÜÂìÅ‰ø°ÊÅØ -->
          <div class="fl product-info clearfix">
            <!-- ÂõæÁâá -->
            <div class="fl product-img">
              <img class="auto-img" :src="item.small_img" alt />
            </div>

            <!-- ÂÜÖÂÆπ -->
            <div class="fl product-content">
              <!-- ÂïÜÂìÅÊ†áÈ¢ò ËßÑÊ†º  -->
              <div class="product-text">
                <div class="clearfix product-name">
                  <span class="product-title fl">{{item.name}}</span>
                  <span class="fl product-rule">{{item.rule}}</span>
                </div>
                <div class="product-enname">{{item.enname}}</div>
              </div>
              <!-- ÂïÜÂìÅ‰ª∑Ê†º Âíå ÈÄâÊã©Êï∞Èáè -->
              <div class="product-price">
                <div class="price fl">Ôø•{{item.price}}</div>
                <div class="fr product-count">
                  <van-stepper
                    v-model="item.count"
                    :integer="true"
                    theme="round"
                    button-size="22"
                    disable-input
                    @change="modifyCount(item)"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>

        <template #right>
          <van-button square type="danger" text="Âà†Èô§" @click="deleteProduct([item.sid], index)" />
          <van-button square type="primary" text="Êî∂Ëóè" />
        </template>
      </van-swipe-cell>
    </div>

    <!-- Êèê‰∫§ËÆ¢Âçï  isManageÁÆ°ÁêÜÁªëÂÆö ÂàáÊç¢-->
    <div v-if="isManage">
      <van-submit-bar class="submit-bar" :price="total" button-text="Êèê‰∫§ËÆ¢Âçï" button-type="info" @submit="goSubOrder">
        <van-checkbox v-model="allCheck" @click="allSelect">ÂÖ®ÈÄâ</van-checkbox>
      </van-submit-bar>
    </div>

    <div v-else>
      <van-submit-bar class="submit-bar submit-bar-delete" button-text="Âà†Èô§ÈÄâÊã©" button-type="info" @submit="removeMoreProduct">
        <van-checkbox v-model="allCheck" @click="allSelect">ÂÖ®ÈÄâ</van-checkbox>
      </van-submit-bar>
    </div>

  </van-list>
</template>

<script>
import "../../assets/css/cart.less";
import { SubmitBar } from 'vant';
export default {
  data() {
    return {
      isHas: false, //ÈªòËÆ§Êó†Êï∞ÊçÆ  //ÂçïÈÄâ Âú®forEachÂ∑≤Ê∑ªÂä†‰∏∫isCheck
      count: 1, //ÊéßÂà∂ÂïÜÂìÅÊï∞Èáè
      cartLimitData: [], //Ë¥≠Áâ©ËΩ¶ÈôêÂà∂Êï∞ÊçÆ
      allCartData: [], //Ë¥≠Áâ©ËΩ¶ÂÖ®ÈÉ®Êï∞ÊçÆ
      //Êà™ÂèñÊéßÂà∂Âä†ËΩΩÊï∞ÊçÆ 5Êù°
      limitData: 6,
      //ÊáíÂä†ËΩΩË¥≠Áâ©ËΩ¶ÂïÜÂìÅÊù° „ÄÇ ÂèåÂêëÁªëÂÆö ÂïÜÂìÅÂàóË°®list
      lazyLoad: {
        isLoading: true, //Â§Ñ‰∫éÂä†ËΩΩÁä∂ÊÄÅ
        isFinished: false, //ÊòØÂê¶ÂÆåÊàêÂä†ËΩΩÊâÄÊúâÊï∞ÊçÆ
        finishedText: "Ê≤°ÊúâÊï∞ÊçÆÂèØÂä†ËΩΩÂï¶~"
      },
      allCheck: false, //ÂÖ®ÈÄâ
      isManage: true, //ÊéßÂà∂ ÁÆ°ÁêÜ Âà†Èô§ÂïÜÂìÅÊù°
      total:0,
    };
  },
  methods: {
    //axiosËé∑ÂèñË¥≠Áâ©ËΩ¶Êï∞ÊçÆ
    getCart() {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "Âä†ËΩΩ‰∏≠...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();

          if (result.data.code == 5000) {
            //ÂÖàÂ§ÑÁêÜÊï∞ÊçÆÊ∑ªÂä†Â±ûÊÄßisCheck ÂÜç Â≠òÂÇ®Ë¥≠Áâ©ËΩ¶Êï∞ÊçÆ ÁÑ∂ÂêéËµãÂÄº
            result.data.result.forEach(v => {
              v.isCheck = false;
            });
            this.allCartData = result.data.result;
            
            //Êà™Âèñ ÈôêÂà∂Êï∞ÊçÆ Áî®‰∫éÊáíÂä†ËΩΩ
            this.cartLimitData = this.allCartData.splice(0, this.limitData);
            
            //Âà§Êñ≠ÊÄªÊï∞Èáè Êà™ÂèñÂêéÊòØÂê¶ËøòÊúâÊï∞ÊçÆ
            if (this.allCartData.length > 0) {
              this.lazyLoad.isLoading = false;
            } else {
              this.lazyLoad.isLoading = true;
              this.lazyLoad.isFinished = true;
              this.lazyLoad.finishedText = "";
            }
            
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //ÊáíÂä†ËΩΩË¥≠Áâ©ËΩ¶Êï∞ÊçÆ Êà™ÂèñÂêéÂâ©‰ΩôÁöÑÊï∞ÊçÆ
    getLoad() {
      
      setTimeout(() => {
        this.cartLimitData.push(...this.allCartData.splice(0, this.limitData));
        if (this.allCartData.length == 0) {
          this.lazyLoad.isLoading = true; //Ââ©‰Ωô‰∏∫0Â∞±‰∏çÂÜçÂä†ËΩΩ
          this.lazyLoad.isFinished = true;
        }
      }, 2000);
    },

    //ÂÖ®ÈÄâ Áä∂ÊÄÅ
    allSelect(){
      this.allCartData.forEach(v=>{ //Êú™Âä†ËΩΩÂá∫Êù•ÁöÑÂïÜÂìÅ ‰πüÈúÄË¶Å ÂÖ®ÈÄâ
        v.isCheck = this.allCheck;
      })
      this.cartLimitData.forEach(v=>{
        v.isCheck = this.allCheck;
      })
      this.sum()
    },
    //ÂçïÈÄâ Áä∂ÊÄÅ
    simpleSelect(){
      this.sum();
      for(let i =0;i<this.cartLimitData.length;i++){
        if(!this.cartLimitData[i].isCheck){ //1‰∏™Êú™ÂãæÈÄâ 
          this.allCheck =false;
          return;
        }
      }
      //ÂÖ®ÈÉ®Ë¥≠Êª°ÁöÑËØù
      this.allCheck = true;
    },

    //ÁÆ°ÁêÜ  ÂèñÂèç
    manage(){
      this.isManage =!this.isManage;
    },
    
    //‰øÆÊîπË¥≠Áâ©ËΩ¶Êï∞Èáè Ê≠•ËøõÂô®change=‚Äò‚Äô
    modifyCount(item){
     
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "Âä†ËΩΩ‰∏≠...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method:'POST',
        url:'/modifyShopcartCount',
        data:{
          appkey: this.appkey,
          sid:item.sid,
          count: item.count,
          tokenString
        }
      }).then(result => {
          this.$toast.clear();
          
          if(result.data.code == 6000){
              this.sum(); //‰øÆÊîπÊï∞ÈáèËÆ°ÁÆóÊÄªÈáëÈ¢ù
          }
          
      }).catch(err => {
          this.$toast.clear();
          
      })
    },

    //Âçï‰∏™Âà†Èô§Ë¥≠Áâ©ËΩ¶ÂïÜÂìÅ Ê†πÊçÆsid
    deleteProduct(sids ,index){
      //Ëé∑ÂèñË¥≠Áâ©ËΩ¶ÂïÜÂìÅsid
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "Âä†ËΩΩ‰∏≠...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method:'POST',
        url:'/removeShopcart',
        data:{
          appkey: this.appkey,
          sids:JSON.stringify(sids),//‰º†ÈÄíÊï∞ÁªÑ„ÄÇÈúÄË¶ÅÂ∫èÂàóÂåñÂîØ‰∏ÄÊ†áËØÜ
          tokenString
        }
      }).then(result => {
          this.$toast.clear();
          
          if(result.data.code == 7000){
            //Âà†Èô§ÂΩìÂâçÁÇπÂáªÁöÑÊï∞ÊçÆ
            this.cartLimitData.splice(index,1);
            this.sum( );
            // this.lazyLoad.finishedText='';
          }
          
      }).catch(err => {
          this.$toast.clear();
          
      })
    },
    // sumbitÁÆ°ÁêÜÂà†Èô§Â§ö‰∏™Ë¥≠Áâ©ËΩ¶ÂïÜÂìÅ
    removeMoreProduct(){
      // ËßÜÂõæÊï∞ÊçÆÂ§ÑÁêÜ Ëé∑ÂèñÂà†Èô§Ë¥≠Áâ©ËΩ¶sid ‰∏∫true
      let sids =[];
      this.cartLimitData.forEach(v=>{
        if(v.isCheck){
          sids.push(v.sid)
        }
      })
      
      //ÂêéÂè∞Êï∞ÊçÆÂ§ÑÁêÜ
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "Âä†ËΩΩ‰∏≠...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method:'POST',
        url:'/removeShopcart',
        data:{
          appkey: this.appkey,
          sids:JSON.stringify(sids),//‰º†ÈÄíÊï∞ÁªÑ„ÄÇÈúÄË¶ÅÂ∫èÂàóÂåñÂîØ‰∏ÄÊ†áËØÜ
          tokenString
        }
      }).then(result => {
          this.$toast.clear();
          
          if(result.data.code == 7000){
            //Âà†Èô§ÂΩìÂâçÁÇπÂáªÁöÑÊï∞ÊçÆ
            for(let i = this.cartLimitData.length-1;i>0;i--){
              if(this.cartLimitData[i].isCheck){
                this.cartLimitData.splice(i,1);
              }
            }
            this.sum( );
            //
            if(this.cartLimitData.length == 0){
              this.allCheck = false;
              this.isManage = true;
              //this.isHas = false;
              this.lazyLoad.finishedText='';
            }
          }
          
      }).catch(err => {
          this.$toast.clear();
          
      })
    },

    //ËÆ°ÁÆóÊÄªÈáëÈ¢ùtotal
    sum(){
      //ÂàùÂßãÂåñtotal
      this.total =0;
      this.cartLimitData.forEach(v=>{
        if(v.isCheck){
          this.total += v.count *v.price*100
        }
      })

    },

    //Ë∑≥ËΩ¨Êèê‰∫§ËÆ¢ÂçïÈ°µ ÈúÄË¶ÅÂ∏¶id Ëé∑ÂèñÈÄâÊã©ÁöÑÂïÜÂìÅ
    goSubOrder(){
      //Ëé∑ÂèñÈÄâÊã©ÂïÜÂìÅÁöÑsid ÁÑ∂ÂêéÈÅçÂéÜÂïÜÂìÅÊääaid Â∏¶ÂèÇÊï∞Ë∑≥ËΩ¨Âà∞order.vue
      let sids =[];
    
      this.cartLimitData.forEach(v=>{
        
        if(v.isCheck){
          sids.push(v.sid);
          this.$router.push({name:'Pay',query:{sids:sids.join('-')}});
        }else{
          this.$toast('ËØ∑ÈÄâÊã©ÈúÄË¶ÅÊèê‰∫§ÁöÑÂïÜÂìÅÂìü');
        }
      })
      
    }
  },

  created() {
    this.getCart();
    //ÈöêËóèÂØºËà™
    this.$store.state.bottomTabbar = false;
  },
  // ÂàáÊç¢ÁªÑ‰ª∂ÁîüÂë®ÊúüÈí©Â≠êÔºåÈîÄÊØÅÂáΩÊï∞
  beforeDestroy() {
    this.$store.state.bottomTabbar = true;
  }
};
</script>

<style lang="scss" scoped>
</style>